@import "Example/style.scss";

@font-face {
  font-family: "Roboto Regular";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: local("Roboto Regular"), local("Roboto-Regular"),
    url("../assets/font/Roboto/Roboto-Regular.ttf") format("truetype");
}

/* Roboto bold */
@font-face {
  font-family: "Roboto Bold";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: local("Roboto Bold"), local("Roboto-Bold"),
    url("../assets/font/Roboto/Roboto-Bold.ttf") format("truetype");
}

body {
  margin: 0;
  padding: 0;
  color: #596060;
  font-weight: 400;
  font-family: "Roboto Regular";

  &.swal2-height-auto {
    height: 100% !important;
  }

  .main-content-wrapper {
    min-height: calc(100vh - 86px - 70px);
    overflow: hidden;

    .clearfix-top {
      height: 90px;
    }

    .wrap-content {
      margin: 1rem;
      padding: 1rem;
      box-sizing: content-box;
      height: 100%;
      border-radius: 4px;
    }
  }
}

a {
  &:hover {
    text-decoration: none !important;
  }
}

::selection {
  background: #d3d3d3;
  color: #555;
}

::-moz-selection {
  background: #d3d3d3;
  color: #555;
}

button:focus {
  outline: none;
}

.h-100 {
  height: 100%;
}

.cursor-pointer {
  cursor: pointer !important;
}

.bg-theme {
  background-size: cover;
  background-repeat: no-repeat;
}

.bg_white {
  background: #ffffff !important;
}

.bg_gray {
  background: gray !important;
}

.wrapper-content {
  padding: 15px 30px 30px;
  background-color: #ffffff;
  border-radius: 4px;
  -webkit-box-shadow: 0px 10px 20px 0px rgba(229, 229, 229, 0.75);
  box-shadow: 0px 10px 20px 0px rgba(229, 229, 229, 0.75);
}
